import React, {useState} from 'react';
import './jenjang.css';

const Jenjang = ({handleClick}) => {
	const [state, setState] = useState('jenjang__entering');

	setTimeout(() => {
		if( state === 'jenjang__entering') setState('jenjang');
	}, 0);

	const buttonClicked = (e) => {
		e.target.classList.add('jenjang-button-clicked');
		setTimeout(() => {
			e.target.classList.remove('jenjang-button-clicked');
		}, 150);
		setTimeout(() => {
			e.target.classList.add('jenjang-button-clicked');
		}, 300);
		setTimeout(() => {
			e.target.classList.remove('jenjang-button-clicked');
			setState('jenjang__exiting');
		}, 450);
		setTimeout(() => {
			handleClick(e.target.value);
		},950)
	}

	  return (
		   <div className={`${state}`}>
			   	<h2 className="jenjang__question">Jenjang kamu sekolah?</h2>
		   	<div className="jenjang__button-wrapper">
		   		<button className="jenjang__button" onClick={(e) => buttonClicked(e)} value="SD">SD</button>
		   		<button className="jenjang__button" onClick={(e) => buttonClicked(e)} value="SMP">SMP</button>
		   		<button className="jenjang__button" onClick={(e) => buttonClicked(e)} value="SMA">SMA</button>
		   	</div>
		   </div>
	  )
}

export default Jenjang;