import React, {useState} from 'react';
import Welcome from './component/welcome';
import Jenjang from './component/jenjang';
import Kelas from './component/kelas';
import Form from './component/form';
import Confirmation from './component/confirmation';
import Endscreen from './component/endscreen';

const App = () => {

	const [component, setComponent] = useState('welcome');
	const [jenjangVoter, setJenjangVoter] = useState(null);
	const [kelasVoter, setKelasVoter] = useState(null);
	const [favorit1, setFavorit1] = useState(null);
	const [favorit2, setFavorit2] = useState(null);
	const [favorit3, setFavorit3] = useState(null);

	const handleClickWelcome  = () => {
		setComponent('jenjang');
	}

	const handleClickJenjang = (jenjang) => {
		console.log(jenjang);
		setJenjangVoter(jenjang);
		setComponent('kelas');
	}

	const handleClickKelas = (kelas) => {
		console.log(kelas);
		setKelasVoter(kelas);
		setComponent('form');
	}

	const handleClickForm = (fav1,fav2,fav3) => {
		setFavorit1(fav1);
		setFavorit2(fav2);
		setFavorit3(fav3);
		setComponent('confirmation');
	}

	const handleClickConfirmation = (yesOrNo) => {
		if(yesOrNo) {
			setComponent('endscreen')
		} else {
			setComponent('form')
		}
	}

	const handleClickEndscreen = () => {
		setComponent('welcome')
	}


	return (
		<div className="component">
				{component === 'welcome' ? <Welcome handleClick = {handleClickWelcome} /> : null}
				{component === 'jenjang' ? <Jenjang handleClick = {(jenjang) => handleClickJenjang(jenjang)} /> : null}
				{component === 'kelas' ? <Kelas jenjang={jenjangVoter} handleClick = {(kelas) => handleClickKelas(kelas)} /> : null}
				{component === 'form' ? <Form handleClick = {(fav1,fav2,fav3) => handleClickForm(fav1,fav2,fav3)} /> : null}
				{component === 'confirmation' ? <Confirmation fav1={favorit1} fav2={favorit2} fav3={favorit3} next={() => handleClickConfirmation(true)} back={() => handleClickConfirmation(false)} /> : null}
				{component === 'endscreen' ? <Endscreen handleClick = {() => handleClickEndscreen()} /> : null}
		</div>
	  )
}

export default App;