import React, {useState} from 'react';
import './welcome.css';

const Welcome = ({handleClick}) => {

	const [state, setState] = useState('welcome__entering');

	setTimeout(() => {
		if( state === 'welcome__entering') setState('welcome');
	}, 0);

	const startButtonClicked = (e) => {
		
		e.target.classList.add('start-button-clicked');
		setTimeout(() => {
			e.target.classList.remove('start-button-clicked');
		}, 150);
		setTimeout(() => {
			e.target.classList.add('start-button-clicked');
		}, 300);
		setTimeout(() => {
			e.target.classList.remove('start-button-clicked');
			setState('welcome__exiting');
		}, 450);
		setTimeout(() => {
			handleClick();
		},950)
	}

	return (
		<div className={`${state}`}>
				<div className="welcome__text">
					<h2 className="welcome__title">Pemilihan Best MT of the Month</h2>
					<h3 className="welcome__description">Brain Academy Pontianak</h3>
				</div>
				<button className="welcome__start-button" onClick={(e) => startButtonClicked(e)}><h2>Mulai</h2></button>
					
		</div>
	  )
}

export default Welcome;