import React, {useState} from 'react';
import './endscreen.css';

const Endscreen = ({handleClick}) => {

	const [state, setState] = useState('endscreen__entering');

	setTimeout(() => {
		if( state === 'endscreen__entering') setState('endscreen');
	}, 0);

	const endButtonClicked = (e) => {
		
		e.target.classList.add('end-button-clicked');
		setTimeout(() => {
			e.target.classList.remove('end-button-clicked');
		}, 100);
		setTimeout(() => {
			e.target.classList.add('end-button-clicked');
		}, 200);
		setTimeout(() => {
			e.target.classList.remove('end-button-clicked');
			setState('endscreen__exiting');
		}, 300);
		setTimeout(() => {
			handleClick();
		},950)
	}

	return (
		<div className={`${state}`}>
				<div className="endscreen__text">
					<h2 className="endscreen__title">Terima kasih Brainers yang telah memilih MT favoritnya</h2>
					<h3 className="endscreen__description">Nantikan hasilnya di media sosial BA Pontianak</h3>
				</div>
				<button className="endscreen__start-button" onClick={(e) => endButtonClicked(e)}><h2>Kembali</h2></button>
					
		</div>
	  )
}

export default Endscreen;