import React, {useState} from 'react';
import './confirmation.css';

const Confirmation = ({fav1,fav2,fav3,next,back}) => {
	
	const [state, setState] = useState('confirmation__entering');

	setTimeout(() => {
		if( state === 'confirmation__entering') setState('confirmation');
	}, 0);


	const buttonClicked = (yesOrNo) => {
		if(yesOrNo) {
			setState('confirmation__exiting');
			setTimeout( () => {
				next()
			},1000)
			
		} else {
			back()
		}
	}

	return (
		<div className={`${state}`}>
			<h3 className="confirmation__question">{`MT favorit kamu adalah kak ${fav1}, kak ${fav2} dan kak ${fav3}?`}</h3>
		   	<div className="confirmation__button-wrapper">
		   		<button className="confirmation__button" onClick={() => buttonClicked(true)} >Iya</button>
		   		<button className="confirmation__button" onClick={() => buttonClicked(false)} >Ubah pilihan</button>
		   	</div>
		</div>
	)
}

export default Confirmation;